<template>
  <div id="header">
    <div class="navbar">
      <div class="title"> <span>{{this.$t('accessSystem.kaolamenjin')}}</span></div>
      <el-button round plain size="medium" class="exist" @click="exist">{{this.$t('accessSystem.tuichuxitong')}}</el-button>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      
    }
  },
  methods:{
    exist(){
      this.$router.push('/workbench/workbench')
    }
  }
};
</script>
<style scoped>
  .navbar{
    height: 60px;
    width: 100%;
    
    font-family: PingFang-SC-Bold;
    font-weight:bold;
  }
  .title{
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    height: 60px;
    font-size:24px;
   
  }
  .title:after{
  content:'';
  width:0;
  height:100%;
  display:inline-block;
  vertical-align:middle;
}
.exist{
    float: right;
    margin-top: 10px;
    vertical-align: middle;
  }

</style>





