<template>
  <div id='main'>
    <el-container>
      <el-header>
        <klHeader></klHeader>
      </el-header>
      <el-container>
        <el-aside
          width="200px"
          class="aside"
        >
        <klSide></klSide>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import klHeader from "./header.vue";
import klSide from './side'
export default {
  components: {
    klHeader: klHeader,
    klSide: klSide
  }
};
</script>
<style >
.el-header {
  background-color: white;
  box-shadow: 0px 2px 10px 0px rgba(211, 211, 211, 1);
  z-index: 99
}
.aside {
  width: 100%;

  background-color:white;
  box-shadow: 0px 2px 10px 0px rgba(211, 211, 211, 1);
}
.el-main {
  position: relative;
  padding: 20px 1.28rem;
}
</style>




