<script>

export default {
  data() {
    return {
      isCollapse: false
    };
  }
};
</script>
<style scoped>
.el-menu-vertical-demo {
  width: 200px;
}
.el-menu-item{
  min-width: 0
}
.el-menu{
    border-right-width: 0;
}
</style>



